import React from "react";
import "./styles.css";
import "bootstrap/dist/css/bootstrap.css";

export default function NumberEditor(props) {
  function onChizzle(e) {
    props.onChange(e.target.value);
  }
  return (
    <div className="row">
      <div className="col-xs-4 col-md-4">
        <label className="" htmlFor="val">
          {props.label}
          <span className="ml-1 small text-muted">({props.pointValue})</span>
        </label>
      </div>
      <div className="col-xs-4 col-md-4">
        <input
          min="0"
          className="form-control"
          value={props.value}
          name={props.id}
          type="number"
          onChange={onChizzle}
        />
      </div>
    </div>
  );
}
